<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import { updatepassword } from '../../../../services/customer'
/**
 * Profile component
 */
export default {
  page: {
    title: 'Profile',
    meta: [{ name: 'description', content: appConfig.description }],
  },
  components: {
    Layout,
    PageHeader,
  },
  data() {
    return {
      password: '',
      repeatpassword: '',
      topUp: false,
      title: 'Profile',
      items: [
        {
          text: 'Home',
          href: '/',
        },
        {
          text: 'Profile',
          active: true,
        },
      ],
    }
  },
  computed: {
    getUserDetails() {
      return this.$store.getters['auth/loggedInDetails']
    },
  },
  methods: {
    changepassword() {
      this.topUp = true
    },
    async updatepassword() {
      try {
        const payload = {
          password: this.password,
          repeatPassword: this.repeatpassword,
        }
        const result = await updatepassword(
          this.getUserDetails.officeId,
          payload
        )
        if (result) {
          this.form = {}
          this.query = ''
          this.$swal({
            group: 'alert',
            type: 'success',
            text: `Your Password Changed Successfully`,
            duration: 5000,
          })
        }
      } catch (e) {
        this.form = {}
        this.query = ''
        this.$notify({
          group: 'alert',
          type: 'error',
          text: `${e.message}`,
          duration: 5000,
        })
      }
    },
  },
}
</script>

<template>
  <Layout>
    <PageHeader :title="title" :items="items" />
    <div class="row">
      <div class="col-lg-12">
        <div class="card">
          <div class="card-body">
            <div class="text-center mt-3">
              <!-- <img
					src="data:image/png;base64,"
					alt
					class="avatar-lg rounded-circle"
              />-->
              <h5 class="mt-2 mb-0">{{ $attrs.user.username }}</h5>
            </div>
            <div class="col-md-12">
              <button
                style="float:right;background-color:#26a69a"
                type="submit"
                class="btn btn"
                @click="changepassword"
              >Change Password</button>
            </div>
            <!-- profile  -->
            <div class="mt-5 pt-2 border-top">
              <h4 class="mb-3 font-size-15">About</h4>
              <p class="text-muted mb-4">Hi I'm Crown.</p>
            </div>
            <div class="mt-3 pt-2 border-top">
              <h4 class="mb-3 font-size-15">Contact Information</h4>
              <div class="table-responsive">
                <table class="table table-borderless mb-0 text-muted">
                  <tbody>
                    <tr>
                      <th scope="row">Email</th>
                      <td>trigital@gmail.com</td>
                    </tr>
                    <tr>
                      <th scope="row">Phone</th>
                      <td>(123) 123 1234</td>
                    </tr>
                    <tr>
                      <th scope="row">Address</th>
                      <td>
                        1975 Boring Lane, San Francisco, California, United States -
                        94108
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
        <b-modal v-model="topUp" title="ChangePassword" title-class="font-18" hide-footer>
          <div class="form-group">
            <label class="form-control-label">Enter Password</label>
            <div class="input-group input-group-merge">
              <div class="input-group-prepend">
                <span class="input-group-text">
                  <feather type="lock" class="align-middle icon-dual"></feather>
                </span>
              </div>
              <b-form-input
                id="input-1"
                v-model="password"
                type="text"
                required
                placeholder="Enter your new password"
              ></b-form-input>
            </div>
          </div>
          <div class="form-group mt-4">
            <label class="form-control-label">Confirm Password</label>
            <div class="input-group input-group-merge">
              <div class="input-group-prepend">
                <span class="input-group-text">
                  <feather type="lock" class="align-middle icon-dual"></feather>
                </span>
              </div>
              <b-form-input
                id="password"
                v-model="repeatpassword"
                type="text"
                required
                placeholder="confirm your new password"
              ></b-form-input>
            </div>
          </div>
          <b-button class="mt-3" variant="primary" block style="background-color:#26a69a" @click="updatepassword">Update</b-button>
          <!-- <b-button variant="primary" @click="updatepassword">Updatepassword</b-button> -->
        </b-modal>
        <!-- end card -->
      </div>
    </div>
  </Layout>
</template>
